import * as React from 'react';
import { connect } from 'react-redux';
import { addPokemon, selectPokemon } from 'actions';
export const AddPokemonButtonBase = ({ onClick }) => (React.createElement("button", { className: 'pt-intent-success pt-button add-new-pokemon', onClick: e => {
        e.preventDefault();
        onClick && onClick();
    } },
    React.createElement("span", { className: 'pt-icon-add' }),
    " \u00A0Add New Pokemon"));
export const AddPokemonButton = connect(null, (dispatch, ownProps) => ({
    onClick: () => {
        dispatch(addPokemon(ownProps.defaultPokemon));
        dispatch(selectPokemon(ownProps.defaultPokemon.id));
    },
}))(AddPokemonButtonBase);
